.nav__wrapper {
    position: -webkit-sticky;
    position: sticky;
    /* height: 100%; */
    height: 600px;
    padding: 130px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
}

.nav {
    /* margin: 0 0 100px 30px; */
    margin: 0 0 30px 30px;
    padding-left: 0;
    list-style: none;
}

.nav__counter {
    font-size: 24px;
    -webkit-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
}

.nav__title {
    font-size: 32px;
    font-weight: 300;
    margin: 0 0 0.25em;
    width: 300px;
    height: 0;
    overflow: hidden;
    opacity: 0;
    -webkit-transition: height 0.3s ease-out, opacity 0.2s ease-out;
    transition: height 0.3s ease-out, opacity 0.2s ease-out;
}

.nav__body {
    font-weight: 100;
    font-size: 18px;
    font-size: 18px;
    width: 300px;
    height: 0;
    overflow: hidden;
    opacity: 0;
    -webkit-transition: height 0.3s ease-out, opacity 0.2s ease-out;
    transition: height 0.3s ease-out, opacity 0.2s ease-out;
}

.nav li {
    position: relative;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    margin-bottom: 1em;
}

.nav li:after {
    content: "";
    display: block;
    border-left: 2px solid white;
    border-top: 2px solid white;
    height: 250px;
    width: 20px;
    position: absolute;
    left: -30px;
    top: 15px;
}

.nav li a {
    display: block;
    padding: 0;
    color: #fff;
    -webkit-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
    text-decoration: none;
}

.nav li a:hover {
    background-color: transparent;
    padding-left: 1em;
}

.nav li a:focus {
    background-color: transparent;
}

.nav li.active {
    pointer-events: none;
    padding-left: 1em;
}

.nav li.active:after {
    width: 35px;
    height: 400px;
    top: 35px;
}

.nav li.active .nav__counter {
    font-size: 48px;
}

.nav li.active .nav__title {
    height: 40px;
    opacity: 1;
    overflow: visible;
}

.nav li.active .nav__body {
    height: 100px;
    opacity: 1;
    overflow: visible;
}
