.header {
    display: flex;
    flex-direction: row;
    position: fixed;
    height: calc(1vh*100);
    height: calc(var(--vh, 1vh)*100);
    z-index: 50;
    width: 100%;
    left: 0;
    top: 0;
    font-size: 16px;
    overflow-x: hidden;
    overflow-y: scroll;
}

.main-page {
    flex-grow: 1;
    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: black;
    display: inline-block;
}

.side {
    z-index: 60;
    position: fixed;
    display: flex;
    background-color: #00B6FF;
}

.side-bar {
    height: 100vh;
    position: relative;
    -webkit-transition: color 0.6s ease;
    transition: color 0.6s ease;
    align-items: stretch;
    width: 6.785rem;
    display: flex;
    flex-direction: column;
}

.sidebar-button {
    position: relative;
    height: 175px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
}

.sidebar-button div {
    height: 5px;
    width: 40%;
    background-color: #1d1d1d;
    margin-bottom: 8px;
}

.sidebar-button div:nth-child(2) {
    width: 25%;
    -webkit-transition: width .7s ease;
    transition: width .7s ease;
}

.nav-menu {
    width: 0;
    position: relative;
    font-family: 'Times New Roman Special', Times, serif;
    flex-direction: row;
    height: 100vh;
    color: black;
    overflow: hidden;
    background-color: #00B6FF;
}

.nav-show {
    display: flex;
}

.menu-category {
    padding-left: 5rem;
    padding-right: 5rem;
    padding-top: 0;
    padding-bottom: 0;
    flex: 1 1;
    display: flex;
    flex-direction: row;
    overflow-x: hidden;
    overflow-y: scroll;
}

.menu-stretch {
    padding-left: 3.725rem;
    padding-right: 3.725rem;
    margin-top: 10.25rem;
    margin-bottom: 3rem;
    font-size: 4rem;
    display: flex;
    flex-direction: column;
    font-family: 'Crimson Text', serif;
}

.menu-item {
    display: flex;
    flex-direction: row;
    margin-bottom: 2rem;
}

.menu-category .menu-stretch ul {
    display: flex;
    flex-direction: row;
    font-size: 3rem;
    padding-left: 3.5rem;
}

.menu-category .menu-stretch ul li {
    line-height: 1.5;
    margin-right: 2rem;
}

.menu-category .menu-stretch ul li:nth-child(1) i {
    color: #0845c9;
}

.menu-category li:nth-child(2) i {
    color: #96c8db;
}

.menu-category li:nth-child(3) i {
    color: rgb(90, 18, 30);
}

.menu-category li:nth-child(5) i {
    color: rgb(228, 9, 46);
}

.menu-stretch a svg {
    height: 3rem;
    width: 3rem;
    padding: 0 2.25rem;
    margin-top: 1.25rem;
    font-size: 3rem;
}

.inner-container {
    display: block;
    width: 25%;
    height: calc(100vh);
    margin: 0;
    position: fixed;
    right: 0;
    top: 0;
}

.inner-container li {
    position: fixed;
    max-width: 100%;
    max-height: calc(100vh + 30rem);
}

.take-cover {
    background-color: #00B6FF;
    z-index: 10;
    min-width: 40rem;
    display: none;
    min-height: calc(100vh + 15rem);
}

.inner-container li img {
    width: 100%;
    height: 100%;
    min-width: 30rem;
    min-height: calc(100vh + 11.25rem);
}
